import React, { Component } from 'react'
import Footer from './Footer';
import Nav from './Nav';
import './../css/gallery.css';
import data from './Data.json';
export default class Gallery extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataJson: [],
            flag:['true','false','false','false'],
            bgcolor: ['active', '', '', ''],
            numberInit: 1,
            numberEnd: 9
        }
    }
    componentDidMount() {
            this.setState({
                dataJson: data
            })
    }
    handleClick1 = () => {
        if(this.state.flag[0] !== true)
        {
            this.setState({
                flag:['true','false','false','false'],
                bgcolor: ['active', '', '', ''],
                numberInit: 1,
                numberEnd: 9
            })
            
        }
    }
    handleClick2 = () => {
        if(this.state.flag[1] !== true)
        {
            this.setState({
                flag:['false','true','false','false'],
                bgcolor: ['', 'active', '', ''],
                numberInit: 1,
                numberEnd: 3
            })
            
        }
    }
    handleClick3 = () => {
        if(this.state.flag[2] !== true)
        {
            this.setState({
                flag:['false','false','true','false'],
                bgcolor: ['', '', 'active', ''],
                numberInit: 4,
                numberEnd: 6
            })
            
        }
    }
    handleClick4 = () => {
        if(this.state.flag[0] !== true)
        {
            this.setState({
                flag:['false','false','false','true'],
                bgcolor: ['', '', '', 'active'],
                numberInit: 6,
                numberEnd: 9
            })
            
        }
    }
    render() {
        console.log(this.state.dataJson);
        return (
            <div>
                <Nav />
                <div className="Gallery-pageheader text-center">
                    <h1>Gallery - Sanjalika Water Park</h1>
                    <ul className="breadcrumb">
                        <li className="breadcrumb-item home">
                            <a href="#" title="Homepage">Homepage</a>
                        </li>
                        <li className="breadcrumb-item active">Gallery</li>
                    </ul>
                </div>
                <div className="Gallery-content">
                    <div className="container">
                        <section>
                            <ul>
                                <li onClick={() => this.handleClick1()} className={"list " + this.state.bgcolor[0]}>All</li>
                                <li onClick={() => this.handleClick2()} className={"list " + this.state.bgcolor[1]}>Dragon Park</li>
                                <li onClick={() => this.handleClick3()} className={"list " + this.state.bgcolor[2]}>Water Park</li>
                                <li onClick={() => this.handleClick4()} className={"list " + this.state.bgcolor[3]}>Other</li>
                            </ul>
                            <div className="product">
                                {data.map((val, key) =>{
                                    if(val.id >= this.state.numberInit && val.id <= this.state.numberEnd)
                                    {
                                        return(<div className="itemBox Dragon-Park"><a href="#"><img alt="" src={val.image} key={key}/></a></div>)
                                    }
                                    else
                                    {
                                        return ''
                                    }
                                    
                                })}
                            </div>
                        </section>
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}
